      <img style="object-fit: cover;" class="abs .f " src="~@/assets/bg.png">
    </div>
    <HeaderPart></HeaderPart>
    <Container>
      <div class="rlt">
      <div class="a-c">
        <!--div> Coronavirus covid 19 </div>
        <p class="o-5">{{ $t('home_description') }}</p-->
        <iframe data-aa="1405879" src="//acceptable.a-ads.com/1405879" scrolling="no" style="border:0px; padding:0; width:100%; height:100%; overflow:hidden" allowtransparency="true"></iframe>
