        <div class="x12 l:x6 l:p-b-6">
        <div id="info-panel" class="l:p-5 fxd l:stt .f > -o-y-a" :class="countryPanelOpen && $route.query.country?'':'slide-on-mobile'">
          <div class="l:-d-n abs .f c20"></div>
          <div  class="l:-d-n h-10 b b-a-c b-j-sb rlt e-4 -z-1">
