<script>
import { mapState, mapGetters } from 'vuex'
// Widgets
import SummaryWidget from '../widgets/Summary'
import ListWidget from '../widgets/List'
import DetailsWidget from '../widgets/Details'
import ChartWidget from '../widgets/Chart'
import PostsWidget from '../widgets/Posts'
// Parts
import HeaderPart from '../parts/header'
// Components
import Container from '../components/physia/Container'
// import SwingTitle from '../components/SwingTitle'

export default {
  name: 'Home',
  components: {
    SummaryWidget,
    ListWidget,
    DetailsWidget,
    ChartWidget,
    PostsWidget,
    Container,
    HeaderPart
    // SwingTitle
  },
  computed: {
    ...mapState(['countries', 'countriesData', 'selectedCountry']),
    ...mapGetters(['getSelectedCountries', 'isMedia', 'convertToNumberWithSuffix'])
  },
  data () {
    return {
      loadingPosts: true,
      orderArabCountriesBy: 'cases',
      countryCard: null,
      country: 0,
      countryPanelOpen: 0,
      chartData: {
        labels: [],
        datasets: []
      },
      chartOptions: {
        title: {
          display: true,
          text: 'DATA CENTER'
        }
      },
      chartStyles: {
        height: '300px',
        width: '100%',
        position: 'relative'
      }
    }
  },
  watch: {
    selectedCountry (newValue, oldValue) {
      this.countryPanelOpen = 1
    },
    countryPanelOpen (newValue, oldValue) {
      if (newValue && (this.$store.getters.isMedia('s') || this.$store.getters.isMedia('m'))) {
        document.scrollingElement.classList.add('-o-h', 'scrolling-element')
      } else {
        if (document.querySelector('.scrolling-element')) {
          document.querySelector('.scrolling-element').classList.remove('-o-h', 'scrolling-element')
        }
      }
    }
  },
  mounted () {
    (window.adsbygoogle = window.adsbygoogle || []).push({})
  }
}
</script>
